// Á±ªÂºèÁªÑ‰ª∂
class Home extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      num:0,
    }
  }
  render(){
    return (
      <div>
        <span>Hello React</span>
        <span>ÊàëÂΩìÂâçÁ≠â‰∫é:{this.state.num}</span>
        <button onClick={this.handleClick.bind(this)}>ÁÇπÊàë</button>
      </div>
    )
  }
  handleClick(){
    this.setState({
      num:this.state.num+1
    })
  }
}

ReactDOM.render(<Home title={"cz"}/>,document.querySelector('#root'))
