import Component from "../react/Component";

// ÂàõÂª∫ÁªÑ‰ª∂
function createComponent(comp,props){
  // Âå∫ÂàÜÂáΩÊï∞ÂΩ¢ÂºèÂíåÁ±ªÂΩ¢Âºè
  let instance;
  if(comp.prototype && comp.prototype.render){ // Á±ªÁªÑ‰ª∂
    instance = new comp();
    console.log(instance);
  }else{ // ÂáΩÊï∞ÁªÑ‰ª∂(Â∞ÜÂÖ∂ËΩ¨Êç¢‰∏∫Á±ªÁªÑ‰ª∂)
    instance = new Component(props);
    instance.constructor = comp;
    instance.render = function(){
      return this.constructor(props);
    }
  }
  return instance;
}
// Ê∏≤Êüì||Êõ¥Êñ∞ÁªÑ‰ª∂
export function renderComponent(comp){
  let base;
  const renderer = comp.render(); // ËôöÊãüdom
  base = _render(renderer) // ËäÇÁÇπ
  // ÊõøÊç¢ËäÇÁÇπ
  if(comp.base && comp.base.parentNode){
    comp.base.parentNode.replaceChild(base,comp.base)
  }
  comp.base = base;
}
// ËÆæÁΩÆÁªÑ‰ª∂Â±ûÊÄß
function setComponentProps(comp,props){
  comp.props = props;
}

// Ê∏≤ÊüìÈ°µÈù¢
function render(vnode,container){ // ÂèÇÊï∞ÔºöËôöÊãüdom,ÂÆπÂô®ËäÇÁÇπ
  return container.appendChild(_render(vnode))
}
// Â∞ÜËôöÊãüdomËΩ¨Âåñ‰∏∫ÁúüÂÆûdom
function _render(vnode){ 
  if(vnode === undefined || typeof vnode === 'boolean'|| vnode===null) return;
  if(typeof vnode === 'number') vnode = String(vnode);
  // Â¶ÇÊûúVodeÊòØ‰∏Ä‰∏™ÊñáÊú¨ËäÇÁÇπ
  if(typeof vnode === 'string'){
    return document.createTextNode(vnode);  
  }
  const { tag,attrs,childrens } = vnode;
  // Â¶ÇÊûú‰∏∫ÁªÑ‰ª∂
  if(typeof tag === 'function'){
    // ÂàõÂª∫ÁªÑ‰ª∂
    const comp = createComponent(tag,attrs);
    // ËÆæÁΩÆÁªÑ‰ª∂Â±ûÊÄß
    setComponentProps(comp,attrs);
    // Ê∏≤ÊüìÁªÑ‰ª∂
    renderComponent(comp);
    // ËøîÂõûÁªÑ‰ª∂ÂæÖÊ∏≤ÊüìÁöÑËäÇÁÇπ
    return comp.base;
  }
  const dom = document.createElement(tag);
  if(attrs){
    Object.keys(attrs).forEach(key=>{
      // ËÆæÁΩÆÂ±ûÊÄß
      setAttribute(dom,key,attrs[key]);
    })
  }
  // ÈÄíÂΩíÊ∏≤ÊüìÂ≠êËäÇÁÇπ
  childrens && childrens.forEach(child => {
    render(child,dom);
  });
  return dom;
}
// ËÆæÁΩÆÂ±ûÊÄß
function setAttribute(dom,key,value){
  if(key === 'className'){
    key = 'class';
  }
  // Â¶ÇÊûúÊòØ‰∫ã‰ª∂ÔºöonClick...
  if(/on\w+/.test(key)){
    // ËΩ¨‰∏∫Â∞èÂÜô
    key = key.toLowerCase();
    dom[key] = value || '';
  }else if(key === 'style'){ // ÂÄºÂèØ‰ª•‰∏∫ÂØπË±°ÂíåÂ≠óÁ¨¶‰∏≤
    if(!value || typeof value ==='string'){
      dom.style.cssText = value || '';
    }else if(value && typeof value === 'object'){
      for(let k in value){
        if(typeof value[k] === 'number'){
          dom.style[k] = value[k]+'px';
        }else{
          dom.style[k] = value[k];
        }
      }
    }
  }else{
    if(key in dom){
      dom[key] = value || '';
    }
    if(value){
      // Êõ¥Êñ∞ÂÄº
      dom.setAttribute(key,value);
    }else{
      dom.removeAttribute(key);
    }
  }
}
export default {
  render
}